.Header{
display: flex;
justify-content: space-between;
align-items: center;
padding: 20px;
position: fixed;
width: 100%;
height: 90px;
z-index: 10;
}
.Header-bg{
    background-image: linear-gradient(to top, #4343437e 40%, black 100% );
    transition: 0.789s;
}
.nav-menu{
    display: flex;
}

.nav-menu li{
    padding:20px ;
}

.nav-menu li a{
    font-size: 24px;
    font-weight: 500;
}

.Hamburger{
    display:none;
}

@media screen and (max-width: 1040px) {
    .nav-menu {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100vh;
        background: rgba(0, 0, 0, 0.789);
        position:absolute;
        top: 0;
        left: -100%;
        z-index: -3;
        transition: .789s;
    }
.nav-menu.active{
        left: 0;
    }

.nav-menu li {
    padding: 1rem;
}

.nav-menu li a {
    font-size: 2rem;
}

.Hamburger {
    display: initial;
}
} 