.Pricing{
width: 100%;
padding: 6rem 1rem;
background: black;
}

.card-container{

    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 40px;
}

.card{
    border: 1px solid white;
    color: white ;
    text-align: center;
    padding: 1rem;
}
.card:hover{
    background-color: rgb(19, 19, 19);
}

.card h3{
    font-size: 1.2rem;
    padding: 1rem;
    
}
 p{
    padding: 10px 0;
}
.bar{
    display: block;
    border-bottom: 1px solid white;
    width: 10%;
    margin:1.5rem auto ;
}
.btc{
    font-size: 3rem;
    font-weight: 400;
    padding: 1rem 0;
}

.card  .btn{
    display: block;
    width: 80%;
    margin: 2rem auto;
    border-radius: 15px;
   

}


@media screen and (max-width:740px){
    .card-container{
    
        max-width: 90%;
        margin: auto;
        grid-template-columns: 1fr
       
    }
}