.footer{
    width: 100%;
    bottom: 0%;
    background-color: rgb(21, 20, 20);
}
.footer-container{
max-width:1140px ;
margin: auto;
display: grid;
grid-template-columns: repeat(2,1fr);
grid-gap:40px;
}

.left{
    /* height: 100%; */
    max-width:100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}
.location{
    display: flex;
    margin-bottom: 0.8rem;
}

.location p{
    line-height:30px ;
    padding-left: 10px;
}



.right{
 /* height: 100%; */
 max-width: 100%;
 display:flex;
 flex-direction: column;
 padding: 1rem;
}


 

.social{
    margin-top: 1rem;
}

h4{
    font-size: 1.2rem;
    padding-bottom: 0.7rem;
}
p{
    font-size: 1.2rem;
}


@media screen and (max-width: 640px) { 
    .footer-container{
      
        grid-template-columns: 1fr;
        
        
        }
}